"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Minus, Plus, Trash2, ChevronRight } from "lucide-react";
import TopPicks from "@/components/home/TopPicks";
import { useCart, CartItem } from '@/context/CartContext';
import { formatPrice } from '@/lib/formatPrice';



export default function Cart() {
  const [isClient, setIsClient] = useState(false);
  const [couponCode, setCouponCode] = useState("");
  const [couponApplied, setCouponApplied] = useState(false);
  
  const { 
    cart = [], 
    removeFromCart, 
    updateQuantity: contextUpdateQuantity, 
    getCartTotal 
  } = useCart();

  useEffect(() => {
    setIsClient(true);
  }, []);

  if (!isClient) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-tendercuts-red"></div>
      </div>
    );
  }

  // Calculate values after ensuring we're on the client
  const subtotal = getCartTotal();
  const discount = couponApplied ? Math.round(subtotal * 0.1) : 0;
  const deliveryFee = subtotal > 500 ? 0 : 40;
  const total = subtotal - discount + deliveryFee;

  const updateQuantity = (id: string, newQuantity: number) => {
    contextUpdateQuantity(id, newQuantity);
  };

  const removeItem = (id: string) => {
    removeFromCart(id);
  };

  const applyCoupon = () => {
    if (couponCode.toLowerCase() === "fresh10") {
      setCouponApplied(true);
    } else {
      alert("Invalid coupon code");
    }
  };

  const removeCoupon = () => {
    setCouponCode("");
    setCouponApplied(false);
  };

  return (
    <div className="bg-gray-50">
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-2xl font-bold text-gray-800 mb-6">Your Shopping Cart</h1>

        {cart.length > 0 ? (
          <div className="flex flex-col lg:flex-row gap-6">
            {/* Cart Items */}
            <div className="flex-1">
              <div className="bg-white rounded-lg shadow-sm p-4 mb-6">
                <div className="space-y-4">
                  {cart.map((item: CartItem) => (
                    <div
                      key={item.id}
                      className="flex flex-col sm:flex-row items-start sm:items-center gap-4 py-4 border-b border-gray-200 last:border-0"
                    >
                      <div className="w-full sm:w-20 relative aspect-square rounded-md overflow-hidden flex-shrink-0">
                        <Image
                          src={item.src || '/images/placeholder-product.jpg'}
                          alt={item.name}
                          fill
                          className="object-cover"
                          sizes="(max-width: 768px) 100vw, 20vw"
                        />
                      </div>

                      <div className="flex-1">
                        <Link
                          href={`/fish/${item.id}`}
                          className="font-medium text-gray-800 hover:text-tendercuts-red transition-colors line-clamp-2"
                        >
                          {item.name}
                        </Link>
                        { (item.grossWeight || item.netWeight) && (
                          <p className="text-sm text-gray-600 mt-1">
                            {item.grossWeight && `Gross: ${item.grossWeight}`}
                            {item.grossWeight && item.netWeight && " | "}
                            {item.netWeight && `Net: ${item.netWeight}`}
                          </p>
                        ) }
                        <div className="flex items-center mt-2">
                          <span className="font-semibold text-gray-800">
                            {formatPrice(item.price / 100)}
                          </span>
                          {item.originalPrice && (
                            <span className="ml-2 text-gray-500 text-sm line-through">
                              {formatPrice(item.originalPrice / 100)}
                            </span>
                          )}
                        </div>
                      </div>

                      <div className="flex items-center">
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity - 1)}
                          className="h-8 w-8 flex items-center justify-center text-gray-500 border border-gray-300 rounded-l-md hover:bg-gray-100"
                        >
                          <Minus size={16} />
                        </button>
                        <span className="h-8 px-3 flex items-center justify-center text-gray-800 border-t border-b border-gray-300">
                          {item.quantity}
                        </span>
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity + 1)}
                          className="h-8 w-8 flex items-center justify-center text-gray-500 border border-gray-300 rounded-r-md hover:bg-gray-100"
                        >
                          <Plus size={16} />
                        </button>
                      </div>

                      <div className="text-right">
                        <p className="font-semibold text-gray-800">
                          {formatPrice(item.price * item.quantity / 100)}
                        </p>
                        <button
                          onClick={() => removeItem(item.id)}
                          className="mt-2 text-tendercuts-red hover:text-tendercuts-red/80 text-sm flex items-center"
                        >
                          <Trash2 size={14} className="mr-1" />
                          Remove
                        </button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              {/* Coupon Code */}
              <div className="bg-white rounded-lg shadow-sm p-4 mb-6">
                <h2 className="font-medium text-gray-800 mb-3">Apply Coupon</h2>
                <div className="flex gap-2">
                  <Input
                    placeholder="Enter coupon code"
                    value={couponCode}
                    onChange={(e) => setCouponCode(e.target.value)}
                    disabled={couponApplied}
                    className="flex-1"
                  />
                  {couponApplied ? (
                    <Button
                      variant="outline"
                      className="border-tendercuts-red text-tendercuts-red hover:bg-tendercuts-red/10"
                      onClick={removeCoupon}
                    >
                      Remove
                    </Button>
                  ) : (
                    <Button
                      className="bg-tendercuts-red hover:bg-tendercuts-red/90"
                      onClick={applyCoupon}
                    >
                      Apply
                    </Button>
                  )}
                </div>
                {couponApplied && (
                  <div className="mt-2 text-sm text-green-600">
                    Coupon "FRESH10" applied successfully! You saved {formatPrice(discount / 100)}.
                  </div>
                )}
              </div>
            </div>

            {/* Order Summary */}
            <div className="w-full lg:w-80 flex-shrink-0">
              <div className="bg-white rounded-lg shadow-sm p-4 sticky top-24">
                <h2 className="font-semibold text-lg text-gray-800 mb-4">Order Summary</h2>

                <div className="space-y-3 text-sm">
                  <div className="flex justify-between">
                    <span className="text-lg font-semibold">Subtotal</span>
                    <span className="text-lg font-semibold">{formatPrice(subtotal / 100)}</span>
                  </div>

                  {discount > 0 && (
                    <div className="flex justify-between text-green-600">
                      <span>Discount (10%)</span>
                      <span>-{formatPrice(discount / 100)}</span>
                    </div>
                  )}

                  <div className="flex justify-between">
                    <span className="text-gray-600">Delivery Fee</span>
                    <span>{deliveryFee === 0 ? 'Free' : formatPrice(deliveryFee)}</span>
                  </div>

                  <div className="border-t border-gray-200 pt-3 mt-3"></div>

                  <div className="flex justify-between text-gray-800 font-semibold">
                    <span>Total</span>
                    <span>₹{total}</span>
                  </div>
                </div>

                <Button
                  className="w-full mt-6 bg-tendercuts-red hover:bg-tendercuts-red/90"
                  asChild
                >
                  <Link href="/checkout" className="flex items-center justify-center">
                    Proceed to Checkout
                    <ChevronRight size={16} className="ml-1" />
                  </Link>
                </Button>

                <p className="text-xs text-gray-500 mt-4 text-center">
                  Free delivery on orders above ₹500
                </p>
              </div>
            </div>
          </div>
        ) : (
          <div className="bg-white rounded-lg shadow-sm p-8 text-center">
            <div className="flex justify-center mb-4">
              <Image
                src="/images/empty-cart.png"
                alt="Empty Cart"
                width={120}
                height={120}
              />
            </div>
            <h2 className="text-xl font-semibold text-gray-800 mb-2">Your cart is empty</h2>
            <p className="text-gray-600 mb-6">
              Looks like you haven't added any items to your cart yet.
            </p>
            <Button className="bg-tendercuts-red hover:bg-tendercuts-red/90" asChild>
              <Link href="/">Continue Shopping</Link>
            </Button>
          </div>
        )}

        {/* Recommended Products */}
        <div className="mt-12">
          <h2 className="text-2xl font-bold text-gray-800 mb-6">You may also like</h2>
          <TopPicks />
        </div>
      </div>
    </div>
  );
}
